

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Host,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Renderer2,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { InputBoolean, TDSSafeAny } from 'tds-ui/shared/utility';
import { ButtonSize, TDSConfigKey, TDSConfigService, WithConfig } from "tds-ui/core/config";



import { badgePresetColors } from './preset-colors';
import { TDSBadgeStatusType } from './types';
import { zoomBadgeMotion } from 'tds-ui/core/animation';
import { TDSNoAnimationDirective } from 'tds-ui/core/no-animation';

const TDS_CONFIG_MODULE_NAME: TDSConfigKey = 'badge';

@Component({
  selector: 'tds-badge',
  exportAs: 'tdsBadge',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [zoomBadgeMotion],
  template: `
  <div class="box-border m-0 p-0 relative inline-block leading-none">
  <ng-container *ngIf="(status || (color && !standalone)) && !dot">
      <span
        class="tds-badge-status-dot {{ colorStatus || presetColor }} relative -top-px inline-block  align-middle rounded-full"
        [style.background]="!presetColor && color"
        [ngClass]="{'w-2 h-2':size =='md','w-1.5 h-1.5':size == 'sm','w-2.5 h-2.5':size =='lg'}"
        [ngStyle]="tdsStyle"
      ></span>
      <span class="tds-badge-status-text ml-2" *ngIf='text'>
        <ng-container *tdsStringTemplateOutlet="text">{{ text }}</ng-container>
      </span>
    </ng-container>
    <ng-content></ng-content>
    <ng-container *tdsStringTemplateOutlet="count">
      <tds-badge-sup
        *ngIf="showSup"
        [offset]="offset"
        [title]="title"
        [tdsStyle]="tdsStyle"
        [dot]="dot"
        [size]="size"
        [overflowCount]="overflowCount"
        [disableAnimation]="!!(standalone || status || color || noAnimation?.noAnimation)"
        [count]="count"
        [noAnimation]="!!noAnimation?.noAnimation"
        [standalone]="standalone"
        [class]="tdsClass"
        [placement]="placement"
      ></tds-badge-sup>
    </ng-container>
  </div>

    
    
  `,
  host: {
    '[class.tds-badge-status]': 'status',
    '[class.tds-badge-not-a-wrapper]': '!!(standalone || status || color)',
    '[class.align-middle]': '!!(standalone || status || color) && !status',
    '[class.align-baseline]': 'status',
    'class': "box-border m-0 p-0 relative inline-block leading-none"
  }
})
export class TDSBadgeComponent implements OnChanges, OnDestroy, OnInit {
  readonly _tdsModuleName: TDSConfigKey = TDS_CONFIG_MODULE_NAME;
  static ngAcceptInputType_showZero: BooleanInput;
  static ngAcceptInputType_showDot: BooleanInput;
  static ngAcceptInputType_dot: BooleanInput;
  static ngAcceptInputType_standalone: BooleanInput;
  showSup = false;
  presetColor: string | null = null;
  dir: Direction = 'ltr';
  private destroy$ = new Subject<void>();
  @Input() @InputBoolean() showZero: boolean = false;
  @Input() @InputBoolean() showDot = true;
  @Input() @InputBoolean() standalone = false;
  @Input() @InputBoolean() dot = false;
  @Input() @WithConfig() overflowCount: number = 99;
  @Input() @WithConfig() color?: string = undefined;
  @Input() tdsStyle: { [key: string]: string }  | null = null;
  @Input() text?: string | TemplateRef<void> | null = null;
  @Input() title?: string | null | undefined;
  @Input() status?: TDSBadgeStatusType | string;
  @Input() count?: number | TemplateRef<TDSSafeAny>;
  @Input() offset?: [number, number];
  @Input() tdsClass?: string = "bg-primary-1 dark:bg-d-primary-1 text-white";
  /**
   * dÃ¹ng cho status
   */
  @Input() size: ButtonSize = 'md';
  @Input() placement: 'topLeft' | 'topRight' | 'bottomLeft' | 'bottomRight' = 'topRight';
  constructor(
    public tdsConfigService: TDSConfigService,
    private renderer: Renderer2,
    private cdr: ChangeDetectorRef,
    private elementRef: ElementRef,
    @Optional() private directionality: Directionality,
    @Host() @Optional() public noAnimation?: TDSNoAnimationDirective
  ) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-badge');
  }
  ngOnInit(): void {
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.prepareBadgeForRtl();
      this.cdr.detectChanges();
    });
    this.dir = this.directionality.value;
    this.prepareBadgeForRtl();
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { color, showDot, dot, count, showZero } = changes;
    if (color) {
      this.presetColor = this.color && badgePresetColors.indexOf(this.color) !== -1 ? this.color : null;
    }
    if (showDot || dot || count || showZero) {
      this.showSup = (this.showDot && this.dot) || this.count! > 0 || (this.count! <= 0 && this.showZero);
    }
  }

  private prepareBadgeForRtl(): void {
    if (this.isRtlLayout) {
      this.renderer.addClass(this.elementRef.nativeElement, 'tds-badge-rtl');
    } else {
      this.renderer.removeClass(this.elementRef.nativeElement, 'tds-badge-rtl');
    }
  }

  get isRtlLayout(): boolean {
    return this.dir === 'rtl';
  }

  get colorStatus() {
    if (!!this.status) {
      return this.convertClass()
    }
    return null
  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
  private convertClass() {
    let result = null;

    switch (this.status) {
      case 'primary':
        result = 'bg-primary-1 dark:bg-d-primary-1';
        break;
      case 'secondary':
        result = 'bg-neutral-1-400 dark:bg-d-neutral-1-400';
        break;
      case 'success':
        result = 'bg-success-400 dark:bg-d-success-400';
        break;
      case 'info':
        result = 'bg-info-400 dark:bg-d-info-400';
        break;
      case 'warning':
        result = 'bg-warning-400 dark:bg-d-warning-400';
        break;
      case 'error':
        result = 'bg-error-400 dark:bg-d-error-400';
        break;
      default:
        result = this.status;
        break;

    }
    return result;
  }
}
